/* lato-regular - latin */
@font-face {
  font-family: 'Lato';
  font-style: var(--font-style-normal);
  font-weight: 400;
  src: local(''),
       url('../fonts/lato-v23-latin-regular.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('../fonts/lato-v23-latin-regular.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* lato-700 - latin */
@font-face {
  font-family: 'Lato';
  font-style: var(--font-style-normal);
  font-weight: 700;
  src: local(''),
       url('../fonts/lato-v23-latin-700.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('../fonts/lato-v23-latin-700.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* lato-900 - latin */
@font-face {
  font-family: 'Lato';
  font-style: var(--font-style-normal);
  font-weight: 900;
  src: local(''),
       url('../fonts/lato-v23-latin-900.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
       url('../fonts/lato-v23-latin-900.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: 'Lato';
  font-style: var(--font-style-normal);
  font-size: 1rem;
  line-height: 1,2;
  font-weight: normal;
  color: var(--text-black);
} 

.wrapper {
  max-width: 66.25rem;
  margin: 0 auto;
}

.wrapper-full {
  max-width: 79rem;
  margin: 0 auto;
}

.base-link {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5rem;
  color: var(--link-color);
  text-decoration: none;
}

.base-button {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25rem;
  color: var(--white-color);
  background: var(--link-color);
  padding: 0.75rem 1.25rem;
  text-decoration: none;
}

.link-shape {
  padding-right: 3.5rem;
  background: url(img/shape-ico.png) no-repeat 95% center;
  transition: .3s all;
}



.section-title {
  position: relative;
  padding-bottom: 0.75rem;
  font-weight: 500;
  font-size: 2.25rem;
  line-height: 2.5rem;
  color: var(--text-black);
}

.section-title::after {
  position: absolute;
  bottom: 0;
  left: 0;
  content: '';
  background: var(--link-color);
  width: 15.75rem;
  height: 0.25rem;
}

.modal-bg {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(9, 14, 15, 0.8);
    z-index: 5;
}

.modal-field {
    position: fixed;
    background: var(--white-color);

    top: 50%;
    left: 50%;                         /* 3 */
    transform: translate(-50%, -50%);
    border-radius: 50px;
    box-shadow: 2px 2px 10px rgb(90, 88, 88);
    z-index: 6;
}

.login {
    width: 600px;
    height: 300px;
}

.login-form {
    position: absolute;
    left: 50%;                         /* 3 */
    top: 50%;                         /* 3 */
    transform: translate(-50%, -50%);
}

.login-input {
    border-radius: 1rem;
    padding: 0.75rem 1.25rem;
    margin-bottom: 1.5rem;
    border-color: #d2d9e1;
    box-shadow: 2px 2px 10px rgb(232, 228, 228);
    width: 100%;
}

.login-sign-in {
    position: relative;
    left: 50%;                         /* 3 */
    transform: translate(-50%, 0);
}

.modal-title {

    position: absolute;
    left: 50%;                         /* 3 */
    transform: translate(-50%, 0);
    font-weight: 500;
    font-size: 1.75rem;
    line-height: 2.5rem;
    color: var(--text-black);
    margin: 5px;
}